.task-item{display:flex;justify-content:space-between;align-items:center;background-color:#1e1e1e;padding:15px;margin-bottom:12px;border-radius:10px;border-left:4px solid #7b2cbf;transition:all .3s ease;box-shadow:0 4px 6px rgba(0,0,0,.2)}.task-item:hover{background-color:#252525;transform:translateX(5px);box-shadow:0 4px 12px rgba(123,44,191,.2)}.task-item .task-content{display:flex;align-items:center;gap:15px;cursor:pointer;flex-grow:1}.task-item .task-content .checkbox{width:22px;height:22px;border:2px solid #7b2cbf;border-radius:50%;position:relative;transition:all .3s ease;flex-shrink:0}.task-item .task-content .text{color:#e0e0e0;font-size:16px;margin:0;transition:color .3s ease;word-break:break-word}.task-item .delete-btn{background:rgba(0,0,0,0);border:none;color:#555;font-size:20px;cursor:pointer;padding:5px 10px;transition:all .2s ease;opacity:.7}.task-item .delete-btn:hover{color:#ff4d4d;opacity:1;transform:scale(1.1);text-shadow:0 0 8px rgba(255,77,77,.5)}.task-item.completed{opacity:.6;border-left-color:#444}.task-item.completed .task-content .text{text-decoration:line-through;color:#888}.task-item.completed .task-content .checkbox{background-color:#7b2cbf;box-shadow:0 0 10px #7b2cbf}.task-item.completed .task-content .checkbox::after{content:"✓";position:absolute;color:#fff;font-size:14px;top:50%;left:50%;transform:translate(-50%, -50%)}.task-item.completed:hover{transform:none;box-shadow:none}